.multi-section__cover-section{
    color: white;
    width: 100%;
    font-family: var(--font-family);
    letter-spacing: 2px;

}
.multi-section__cover-section__container{
 
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    align-items: center;
    width: 100%;
}
.container__divider{
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: space-evenly;
    align-items: center;
}
.top___container{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;


}
.top___container h5{
    margin-top: 70px;
    margin-bottom: 70px;
    line-height: 40px;
    background-color: red;
    width: 100px;
    border-radius:20px ;
    border: none;
    cursor: pointer;
    font-family: var(--font-family);
    text-align: center;
}
.anime-list{
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    justify-content: center;
    height: 55vh;
    margin-top: 70px;
    width: 100%;
    
    align-items: flex-start;
    background: rgba( 38, 26, 26, 0.25 );
    box-shadow: 0 8px 32px 0 rgba(135, 31, 31, 0.37);
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.anime{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    height: 100px;
    gap: 20px;
    align-items: center;
    opacity: 0.75;
}
.anime:hover{
   opacity: 1;
}

.anime img{
    width: 60px;
}
.anime p{
    width: 170px;
    color: white;
    font-size: 16px;
}
@media screen and (max-width:1080px) {
    .anime-list{
        height:100%;
        gap: 10px;
    }
    .multi-section__cover-section__container{

    flex-direction: column;
    gap: 30px;
    }
    .container__divider{

        gap: 40px;
    }
    .anime img{
        width: 50px;
        height: 50px;
    }
    .anime p{
        width: 120px;
        font-size: 14px;
        color: white;
    }
}
@media screen and (max-width:880px) {
    .multi-section__cover-section__container{
        flex-direction: column;
    }
    .container__divider{

        gap: 10px;
    }
    .anime-list{
        height: 45vh;
    }
    .anime img{
        width: 50px;
        height: 50px;
    }
    .anime p{
        width: 120px;
        font-size: 14px;
        color: white;
    }
@media screen and (max-width:880px) {
    .multi-section__cover-section__container{
        flex-direction: column;
    }
    .container__divider{
       flex-direction: column;
        gap: 10px;
    }
    .anime-list{
        height: 45vh;
    }

}
}