*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body{
    background-color: var(--color-bg);
    scroll-behavior: smooth;
}
.content {

        /* ff 3.6+ */
        background:-moz-linear-gradient(90deg, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%); 
    
        /* safari 5.1+,chrome 10+ */
        background:-webkit-linear-gradient(90deg, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%);
    
        /* opera 11.10+ */
        background:-o-linear-gradient(90deg, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%);
    
        /* ie 6-9 */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#FF0000', GradientType=0 );
    
        /* ie 10+ */
        background:-ms-linear-gradient(90deg, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%);
    
        /* global 94%+ browsers support */
        background:linear-gradient(90deg, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%);
    
    
        height: 3px;
        margin-bottom: 20px;
        border: none;
    }
.gradient_bg


{

        /* ff 3.6+ */
        background:-moz-linear-gradient(139deg, rgba(56, 162, 215, 1) 7%, rgba(86, 17, 57, 1) 94%); 
    
        /* safari 5.1+,chrome 10+ */
        background:-webkit-linear-gradient(139deg, rgba(56, 162, 215, 1) 7%, rgba(86, 17, 57, 1) 94%);
    
        /* opera 11.10+ */
        background:-o-linear-gradient(139deg, rgba(56, 162, 215, 1) 7%, rgba(86, 17, 57, 1) 94%);
    
        /* ie 6-9 */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#561139', endColorstr='#38A2D7', GradientType=1 );
    
        /* ie 10+ */
        background:-ms-linear-gradient(139deg, rgba(56, 162, 215, 1) 7%, rgba(86, 17, 57, 1) 94%);
    
        /* global 94%+ browsers support */
        background:linear-gradient(139deg, rgba(56, 162, 215, 1) 7%, rgba(86, 17, 57, 1) 94%);
    
    }

.fade-in-right {
	-webkit-animation: fade-in-right 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in-right 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes fade-in-right {
    0% {
      -webkit-transform: translateX(50px);
              transform: translateX(50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
  @keyframes fade-in-right {
    0% {
      -webkit-transform: translateX(50px);
              transform: translateX(50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }

  .flip-2-ver-right-fwd {
    -webkit-animation: flip-2-ver-right-fwd 0.9s ease-in-out both;
            animation: flip-2-ver-right-fwd 0.9s ease-in-out both;
  }

@-webkit-keyframes flip-2-ver-right-fwd {
  0% {
    -webkit-transform: translateX(0) translateZ(0) rotateY(0);
            transform: translateX(0) translateZ(0) rotateY(0);
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
  }
  100% {
    -webkit-transform: translateX(100%) translateZ(160px) rotateY(-180deg);
            transform: translateX(100%) translateZ(160px) rotateY(-180deg);
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
  }
}
@keyframes flip-2-ver-right-fwd {
  0% {
    -webkit-transform: translateX(0) translateZ(0) rotateY(0);
            transform: translateX(0) translateZ(0) rotateY(0);
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
  }
  100% {
    -webkit-transform: translateX(100%) translateZ(160px) rotateY(-180deg);
            transform: translateX(100%) translateZ(160px) rotateY(-180deg);
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
  }
}
