.category__heading{
    color: #ffffff;
    padding: 100px 20px;
    font-family: var(--font-family);
}
.category__heading h4{
    background-color: red;
    
    font-family: var(--font-family);
}
.Schedule_container-main{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}
.Schedule__section__container-section{
    padding: 100px 100px;
    display: flex;
    flex-direction: column;
    color: aliceblue;
    width: 100%;
}
.Schedule__section-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    font-family: var(--font-family);
}
.Schedule__section-container p{
     font-size: 26px;
     letter-spacing: 1px;
}
.more__btn{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Schedule__section-container a{
     font-size: 18px;
     text-decoration: none;
     letter-spacing: 1px;
     width: 100%;
     height: 40px;
     color: #ffffff;
     border-radius: 50px;
     margin-bottom: 10px;
     border-style: none;
     padding: 8px 8px;
     background-color: red;
     opacity: 0.7;
     
}
.Schedule__section-container a:hover{

     opacity: 1;
     
}

.Schedule__section_movie-names{
    display: flex;
    flex-direction: column;
    
    background: rgba( 38, 26, 26, 0.25 );
    box-shadow: 0 8px 32px 0 rgba(135, 31, 31, 0.37);
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
   
}
.time__name{
    display: flex;
    padding: 2px 4px;
    flex-direction: row;
    height: 8vh;
    justify-content: space-around;
    align-items: center;
}
.time__name h5{
    min-width: 100px;
    line-height: 40px;
    font-family: var(--font-family);
    text-align: center;


  
}
.time__name p{
    min-width: 300px;
    line-height: 40px;
    text-align: center;
    font-family: var(--font-family);

  
}
.Section__Categories{
    display: flex;
    padding: 100px 100px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: #ffffff;
    gap: 20px;

}
.sub-category{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    font-family: var(--font-family);
 
    width: 80px;
}
.sub-category p:hover{
   color: red;
}
@media screen and (max-width: 1080px){
    .Schedule_container-main{
        flex-direction: column;
  
    
    }
}
@media screen and (max-width: 1080px){
    .Schedule_container-main{
        flex-direction: column;
  
    
    }
    .Schedule__section__container-section{
        padding: 10px 10px;
        display: flex;
        flex-direction: column;
        color: aliceblue;
        width: 100%;
    }
    .Section__Categories{
        display: flex;
        padding: 20px 20px;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        color: #ffffff;
        gap: 20px;
    
    }
}