.Header_content{
    padding: 10px 20px;
    display: flex;
    flex-direction: column;

    justify-content: center;
}
.Header_content h1{
    font-size: auto;
    font-family: var(--font-family);
    color: rgb(255, 255, 255);
    letter-spacing: 5px;
    margin-bottom: 20px;
}


.slider__Video-display-slideShow{
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: row;
    max-width: 100%;
    padding: relative;
   
}
.Slider__video-display-slideShow__container{
  display: flex;
  flex-direction: row;
  width: 100%;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  
}
.Slider__video-display-slideShow__container::-webkit-scrollbar{
    display: none;
}
.display__gallery-images{
    position: relative;
    min-width: 201px;
    height: 300px;
    margin-right: 2rem;
   
}
.display__gallery-images img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: 0.5 ease;
  
}

.display__gallery-images:hover img{
    opacity: 0.35;
}
.slider__video-display-slideShow__btn{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: -5%;
    padding: 0 1rem;
    position: absolute;
    opacity: 0.7;
}
.slider__video-display-slideShow__btn:hover{
    opacity: 1;
}
.slideShow-btn{
    color: var(--color-font);
    font-size: 2rem;
    cursor: pointer;
    background-color: rgb(233, 224, 224);
    border-radius: 5px;
}
.slideShow-btn:hover{
    color: rgb(0, 0, 0);
}
@media screen and (max-width:1080px){
    .display__gallery-images{
   
        min-width: 201px;
        height: 250px;
    
       
    }
    .slider__video-display-slideShow__btn{
     
        bottom: 60%;
    }
}
@media screen and (max-width:950px){

    .slider__video-display-slideShow__btn{
     
        bottom: 10%;
    }
}
@media screen and (max-width:750px){

    .display__gallery-images{
   
        min-width: 101px;
        height: 150px;
    
       
    }
    .slider__video-display-slideShow__btn{
     
        bottom: 30%;
    }
}
@media screen and (max-width:550px){

    .slider__video-display-slideShow__btn{
     
        bottom: 40%;
    }
}