.Social__icon-container{
    padding: 50px 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.Social__Icon{

    position: relative;
    display: flex;
    align-items: center;
    gap: 70px;
    padding: 40px 40px;
    height: 10vh;
    
}
.Social__Icon img{
    position: relative;
    width: 7rem;
}
.insta {
    background-color: #fa7e1e;

}
.twit{
    background-color: #00acee;

}
.face{
    background-color:#3b5998;

}
.tele{
    background-color: #0088cc;

}
.Social__icon-btn{
    display: flex;
    flex-direction: row;
    gap: 40px;
    font-style: none;
   
}
.Social__icon-btn a{

    align-items: center;
    justify-content: center;
     color: black;
     display: flex;
     text-decoration: none;
     border-radius: 20px;
     font-size: 24px;
     width: 200px;
     line-height: 40px;
     opacity: 0.67;
}
.Social__icon-btn a:hover{
    opacity: 1;
}
.Social__icon-btn a p{

    align-items: center;
    justify-content: center;
    color: rgb(255, 254, 254);
    font-family: var(--font-family);
    margin-left: 10px;
    display: flex;

}
@media screen and (max-width: 1080px) {
    .Social__Icon img{
        width: 4rem;
    }
    .Social__Icon{

  
        gap: 70px;
 
        
    }
    .Social__icon-btn{
     
        gap: 40px;

       
    }
    .Social__icon-btn a {
        width: 50px;
        height: 50px;
       font-size: 24px;
   }

   .Social__icon-btn a p{
       display: none;
        width: 10px;
       font-size: 12px;
       
   }
}
@media screen and (max-width: 600px) {
    .Social__icon-container{
        padding: 40px 2px;
    }
    .Social__Icon img{
        width: 3rem;
    }
    .Social__icon-btn{
     
        gap: 10px;

       
    }
 
    .Social__icon-btn a {
         width: 40px;
         height: 40px;
        font-size: 24px;
    }

    .Social__icon-btn a p{
        display: none;
         width: 10px;
        font-size: 12px;
        
    }
    .Social__Icon{

        gap: 50px;
        padding: 20px 20px;
        
    }
}
