.Comment__container{
    padding:150px 0px ;
    height: 60vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}
.Comments__slider{
    display: flex;
  flex-direction: row;
  width: 100%;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.Comments__slider::-webkit-scrollbar{
    display: none;
}

.Comment__container img{
    width: 35%;
}

.Comment__section{
    padding: 20px 20px;
    display: flex;
    width: 100%;
    flex-direction: column;
    background-color: rgb(0, 0, 0);

}
.Comment__section h1{
    color: #ffffff;
    font-family: var(--font-family);
    letter-spacing: 4px;
    width: 300px;
    border-bottom: 3px solid red;
    padding: 4px 20px;
    border-radius: 0px;
}

.user_name-comment{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    gap: 20px;
}
.user_name-comment img{
    width: 25%;
}

.Comment__container__comment-box{
    height: 300px;
    padding: relative;
    background: rgba(51, 50, 50, 0.25);
box-shadow: 0 8px 32px 0 rgba(196, 20, 20, 0.233);
backdrop-filter: blur( 4px );
-webkit-backdrop-filter: blur( 4px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
    min-width: 250px;
    font-family: var(--font-family);
    font-size: 14px;
    letter-spacing: 1px;
    display: flex;
    flex-direction: column;
    color: #ffffff;
    margin-right: 20px;
}
.comment-box{

    width: 100%;
 padding: 10px 20px;
}

@media screen and (max-width:1080px){ 
    .Comment__container__comment-box{
        height: 300px;
        min-width: 200px;
        margin-right: 10px;
    }
    .Comment__container .sanji__img{
        width: 55%;
    }
}
@media screen and (max-width:780px){ 
    .Comment__container__comment-box{
        height: 300px;
        min-width: 200px;
        margin-right: 10px;
    }
    .Comment__container .sanji__img{
        display: none;
    }
}