
.navbar{
    padding: 25px 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
}
.navbar__logo{
    color: rgb(243, 243, 243);
    font-family: var(--font-logo);
    font-size: 25px;
    stroke: black;
    line-height: 4rem;
    stroke-width: 4px;
    letter-spacing: 2px;
    z-index: 999;
}
.navbar__container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    z-index: 999;
}
.navbar__container-elements{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    line-height: 10px;
    gap: 50px;
    z-index: 999;
}
.navbar__container-elements a{
    text-decoration: none;
    font-family: var(--font-family);
    font-size: 23px;
    letter-spacing: 2px;
    color: var(--color-text);
}
.Search__bar{
    padding: 2px 8px;
    border-style: none;
    border-radius: 12px;
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 20px;
    color:#000000;
    background-color: rgb(245, 245, 245);
    transition: 0.1s ease-in;
    z-index: 999;
}
.navbar__container-Search {
    text-decoration: none;
    font-family: var(--font-family);
    font-size: 26px;
    line-height: 10px;
    color: var(--color-text);
    z-index: 999;
    
}
.navbar__container-sign{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    z-index: 999;
}
.navbar__container-sign a{
    text-decoration: none;
    font-family: var(--font-family);
    font-size: 16px;
    color: var(--color-text);
    z-index: 999;
}
.navbar__container-sign .btn{
    padding: 6px 8px;
    border-radius: 7px;
    font-size: 20px;
    color:#ffffff;
    font-weight: 500;
    letter-spacing: 2px;
    background-color: rgb(255, 1, 1);
    z-index: 999;
}
.navbar__response{
    display: none;
    z-index: 999;
}


@media screen and (max-width:1190px) {
    .navbar__container{
        gap: 15px;
    }
    
    .navbar__logo{
        font-size: 18px;
    }
    .navbar__container-elements{
        gap: 30px;
    }
    .navbar__container-elements a{
    font-size: 18px;
    }
    .navbar__container-sign .btn{
        padding: 2px 4px;
        font-size: 18px;
        background-color: rgb(123, 131, 14);
    }

}
.navbar___response-mobile{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    background: #fcfbfb;
    text-align: end;
    padding: 2rem;
    position: absolute;
    z-index: 9999;
    top: 60px;
    right: 0;
    margin-top:1rem;
    min-width: 160px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.navbar___response-mobile a{
    text-decoration: none;
    font-family: var(--font-family);
    font-size: 23px;
    z-index: 999;
    letter-spacing: 2px;
    color: black;
}
@media screen and (max-width:990px) {
    .navbar__container{
        gap: 10px;
    }
    
    .navbar__logo{
        font-size: 16px;
    }
    .navbar__container-elements{
        gap: 20px;
    }
    .navbar__container-elements a{
    font-size: 14px;
    }
    .navbar__container-sign .btn{
        padding: 2px 4px;
        font-size: 14px;
        background-color: rgb(123, 131, 14);
    }

}
@media screen and (max-width:880px) {
    .navbar{
        padding: 0px 40px;
    }
    .navbar__response{
        display: block;
    }
    .navbar__container-elements{
        display: none;
    }
    .navbar__container-sign{
     display: none;
    }
    

}



