.header__video-content{
    padding: 100px 200px;
}
.Slider_video-slideShow__container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.Slider_video-slideShow__container::-webkit-scrollbar{
    display: none;
}

.Slider1__video__heading{
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    gap: 30px;
    align-items: flex-start;
    overflow: hidden;
    line-height: 1.7rem;
    z-index: 888;
}

.slider3__video video{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 000;
    object-fit: cover;
    max-height: 70vh;
    width: 100%;
    transition: all 10s ease-in-out;

}
.Slider1__video__heading h1{
   color: var(--color-text);
   font-family: var(--font-head);
   font-size: 3.7rem;
   z-index: 888;
}
.Slider1__video__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    z-index: 888;
}
.Slider1__video__content p{
    color: var(--color-text);
    font-size: 18px;
    font-family:var(--font-family);
    
}
.content__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 50px;
    align-items:flex-end;
    z-index: 999;
    padding: 40px 20px;
}
.content__container .active__click__bar{
    width: 100%;
    display: flex;
    left: 0;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    opacity: 0.5;
    z-index: 888;
}
.content__container .active__click__bar:hover{
    opacity: 1;
}

.content__container img{
    width:334px ;
    height: 334px;
    z-index: 888;
    
}
.Slider1__video__btn{
    display: flex;
    gap: 10px;
    align-items: center;
}
.Slider1__video__btn a{
    
    text-decoration: none;
    text-transform: uppercase;
    color: var(--color-text);
    font-size: 16px;
    font-family:var(--font-family)
}
.color__btn{
    text-decoration: none;
    text-transform: uppercase;
    color: var(--color-text);
    font-size: 20px;
    font-family:var(--font-family);
    background-color: rgb(243, 0, 0);
    padding: 6px 8px;
    border-radius: 4px;
}
